import React, { useState, useEffect,useRef } from 'react';
import Axios from 'axios';
import Main from '../componentes/Main';
import Modal from '../componentes/Modal';
import useModal from '../hooks/useModal';
import { Link } from 'react-router-dom';
import Loading from '../componentes/Loading';
import {useAlumno} from '../Context/alumnoContext';
import AbmCurso from '../abms/Abm-curso';
import AbmAlumno from '../abms/abm-alumno';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCopy, faPlusSquare,faCircle, faHandPeace } from '@fortawesome/free-regular-svg-icons';
import { faSync, faCalculator, faEquals, faHashtag, faGreaterThanEqual,faMailBulk,faUserCheck, faUsers, faListOl, faEnvelopeOpen,faEnvelopeSquare, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import Swal from 'sweetalert2';

import {scrollTop, hacerScroll} from '../Helpers/utilidades-globales';


       
export default function Alumnos({match,history}){
    const [cursos,setCursos] = useState([])
    const [cursosAmostrar,setCursosAmostrar]=useState([])
    const {toggle, isShowing } = useModal();
    const [criterio, setCriterio ] = useState('original');


    const [alumnos,setAlumnos]=useState([]);
    const [instrumentos,setInstrumentos]=useState([]);
    const [localidades,setLocalidades]=useState([]);
    const [provincias,setProvincias]=useState([]);
    const [paises,setPaises]=useState([]);

    const [cantidad,setCantidad]=useState(-1);
    const [instrumento,setInstrumento]=useState(-1);
    const [provincia,setProvincia]=useState(-1);
    const [localidad,setLocalidad]=useState(-1);
    const [alumno,setAlumno]=useState(-1);
    const [pais,setPais]=useState(-1);

    const {cambiarAlumno} = useAlumno();


    const [personas, setPersonas ] = useState([]);
    const [tiposPersonal, setTiposPersonal ] = useState([]);
    const [permisos, setPermisos ] = useState([]);
    const [cantidadesCursos, setCantidadesCursos ] = useState([]);
    const [exactamenteIgual, setExactamenteIgual ] = useState(true);

    const [statusMail,setStatusMail]=useState(-1); // 0 Regular 1 Recuperatorio
    const [tipoCurso,setTipoCurso]=useState(-1); // 0 Regular 1 Recuperatorio
    const [cursosRecuperatorios,setCursosRecuperatorios]= useState(-1);
    // para activar el modal llamar a la función toggle en con alguna condicion o un evento...
    const [cargandoCursos,setCargandoCursos] = useState(false);
    const {cuatrimestreActivo,desHabilitarBusquedaAlumnos,habilitarBusquedaAlumnos} = useAlumno();
   // const {alumno, cambiarAlumno} = useAlumno();
    const [crearCurso,setCrearCurso]=useState(false);
    const [cursoAcopiar,setCursoAcopiar]=useState(null);
    const [copiarUnCurso, setCopiarUnCurso] = useState(false);
    const [contadorOperaciones, setContadorOperaciones]= useState(0);
    const [ultimosCursosCreados, setUltimosCursosCreados ]= useState([]);
    const [incluirCursadas,setIncluirCursadas]=useState(false)
    const prueba = match.params.id;

    const [recientes,setRecientes] = useState([]);    

    const [alumnoSeleccionado, setAlumnoSeleccionado] = useState(null)
    const [id_copia, setid_copia] = useState(null)

    const [listaEmails, setListaEmails]=useState([])
    const [listaEmailsSeleccion, setListaEmailsSeleccion]=useState([])
    const [alumnosCopia,setAlumnosCopia]=useState([]);
    const [cargandoCursosAlumno,setCargandoCursosAlumno]=useState(false);

    useEffect(()=>{

    const buscarAlumnos = async ()=>{

        desHabilitarBusquedaAlumnos();

        setCargandoCursos(true)
        try{          
           // const {data} = await Axios.get(`/api/alumnos/listaplus/${incluirCursadas ? 1 : 0}`)
           // setCursos(data)
           // listarUltimoCursosCreados(data,setUltimosCursosCreados)

           const vectorResultado = await Promise.all([Axios.get(`/api/alumnos/listaplus/${incluirCursadas ? 1 : 0}`),
           Axios.get('/api/alumnos/altasrecientes')]);

           setCursos(vectorResultado[0].data)
           setRecientes(vectorResultado[1].data)

           armarListaEmails(vectorResultado[0].data,setListaEmails)

           setCargandoCursos(false)
        }catch(err){
            console.log(err)
            setCargandoCursos(false)
        }
    }

    const buscarAlumnosYcursadas = async ()=>{
        setCargandoCursosAlumno(true)
        try{          
           // const {data} = await Axios.get(`/api/alumnos/listaplus/${incluirCursadas ? 1 : 0}`)
           // setCursos(data)
           // listarUltimoCursosCreados(data,setUltimosCursosCreados)

           const vectorResultado = await Promise.all([Axios.get(`/api/alumnos/listaplus/1`)]);

           setAlumnosCopia(vectorResultado[0].data)

           setCargandoCursosAlumno(false)

        }catch(err){
            console.log(err)
            setCargandoCursosAlumno(false)
        }
    }
        
        buscarAlumnos();
        buscarAlumnosYcursadas();

    },[cuatrimestreActivo,contadorOperaciones])


    
    useEffect(()=>{
            const alumnos = alumnosDelListado();
            setAlumnos(alumnos);

            const instrumentos = instrumentosDelListado();
            setInstrumentos(instrumentos);

            const localidades = localidadesDelListado();
            setLocalidades(localidades);

            const provincias = provinciasDelListado();
            setProvincias(provincias);

            const paises = paisesDelListado();
            setPaises(paises);

            const cantCursos = cantidadCursosDelListado();
            setCantidadesCursos(cantCursos);

            setCursosAmostrar(cursos);
    },[cursos])

  /*  return <>
        <Modal hide={toggle} isShowing={isShowing}>
            <h1>SOY UN MODAL</h1>
        </Modal>
    </>
*/

const irAinscripciones=(alumno)=>{
    seleccionarAlumno(alumno)
    history.push('/cursos')
}

const refrescarLista = ()=>{
    setContadorOperaciones(contadorOperaciones+1)
}

useEffect(()=>{
    armarListaEmails(cursosAmostrar,setListaEmailsSeleccion)
},[cursosAmostrar])

useEffect(()=>{
    if(incluirCursadas){
        setCursos([...alumnosCopia])
    }
},[incluirCursadas])

const buscarCursosSinRenderizar = async ()=>{

    console.log('buscarCursosSinRenderizarbuscarCursosSinRenderizarbuscarCursosSinRenderizar')
    try{          
       // const {data} = await Axios.get(`/api/alumnos/listaplus/${incluirCursadas ? 1 : 0}`)
       // setCursos(data)
       // listarUltimoCursosCreados(data,setUltimosCursosCreados)

       const vectorResultado = await Promise.all([Axios.get(`/api/alumnos/listaplus/${incluirCursadas ? 1 : 0}`),
       Axios.get('/api/alumnos/altasrecientes')]);

       setCursos(vectorResultado[0].data)
       setRecientes(vectorResultado[1].data)

    }catch(err){
        console.log(err)
    }
}

const iniciarCambioDeListado = ()=>{

    Swal.fire({
        text:`¿Está seguro que desea excluir las cursadas?
        para`,
        showCancelButton:true,
        confirButtonText:'Si, excluir las cursadas',
        cancelButtonText:'Cancelar'
    }).then(
        resultado=>{
            if (resultado.value){
                setIncluirCursadas(false);
            }else{
                console.log("Eliminación cancelada")
            }
        }
    )

}
useEffect(()=>{
    if(cantidad==-1){
        setExactamenteIgual(true)
  
    }else{
        modificarListaPorCantidadCursos()
    }
},[cantidad,exactamenteIgual])

function finalizarAltaOcopia (alta,id_nuevo_alumno){

    buscarCursosSinRenderizar() 

    if (alta){
        setid_copia(null)
        setAlumnoSeleccionado({id_alumno:id_nuevo_alumno,nombre:''});
    }
}



const alumnosDelListado = ()=>{

    return cursos.map(item=>item.alumno).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const instrumentosDelListado = ()=>{

    return cursos.map(item=>item.instrumentos).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const localidadesDelListado = ()=>{

    return cursos.map(item=>item.localidad).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const provinciasDelListado = ()=>{

    return cursos.map(item=>item.provincia).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const paisesDelListado = ()=>{

    return cursos.map(item=>item.pais).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const cantidadCursosDelListado = ()=>{

    return cursos.map(item=>item.cursos).sort((a,b)=>a-b).filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const handleChangeSelectAlumno = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setAlumno(e.target.value)
    setProvincia(-1);
    setLocalidad(-1);
    setInstrumento(-1);
    setPais(-1);
    setCantidad(-1);
    setStatusMail(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.alumno===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const handleChangeSelectInstrumento = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(e.target.value) 
    setProvincia(-1);
    setLocalidad(-1);
    setAlumno(-1);
    setPais(-1);
    setCantidad(-1);
    setStatusMail(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.instrumentos===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const iniciarCrearAlumno = ()=>{
    setAlumnoSeleccionado(null);
    toggle();
}

const handleChangeSelectLocalidad = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(-1) 
    setProvincia(-1);
    setLocalidad(e.target.value);
    setAlumno(-1);
    setPais(-1);
    setCantidad(-1);
    setStatusMail(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.localidad===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const handleChangeSelectProvincia = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(-1) 
    setProvincia(e.target.value);
    setLocalidad(-1);
    setAlumno(-1);
    setPais(-1);
    setCantidad(-1);
    setStatusMail(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.provincia===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const handleChangeSelectStatusMail = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(-1) 
    setProvincia(-1);
    setStatusMail(e.target.value);
    setLocalidad(-1);
    setAlumno(-1);
    setPais(-1);
    setCantidad(-1);

    let filtrarVectorCursosOriginal;

    if (e.target.value==0){
        filtrarVectorCursosOriginal = cursos.filter(item=>item.email=='' || item.email==null)
    }else{
        filtrarVectorCursosOriginal = cursos.filter(item=>item.email!=null && item.email!='')
    }

    setCursosAmostrar(filtrarVectorCursosOriginal)

}

const handleChangeSelectCantidad = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(-1) 
    setProvincia(-1);
    setLocalidad(-1);
    setAlumno(-1);
    setPais(-1);
    setCantidad(e.target.value);
    setStatusMail(-1);

}

const handleChangeSelectPais = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setInstrumento(-1) 
    setProvincia(-1);
    setLocalidad(-1);
    setAlumno(-1);
    setPais(e.target.value);
    setCantidad(-1);
    setStatusMail(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.pais===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const procesarSeleccionCantidad = (exactamenteIgual)=>{

    setExactamenteIgual(exactamenteIgual)
}

const seleccionarAlumno = (alumno)=>{

    cambiarAlumno(alumno.id_alumno,alumno.alumno,'nombre','apellido','documento')

}

const modificarListaPorCantidadCursos = ()=>{


    let filtrarVectorCursosOriginal=[];

    if(exactamenteIgual){
        filtrarVectorCursosOriginal = cursos.filter(item=>item.cursos == cantidad)
    }else{
        filtrarVectorCursosOriginal = cursos.filter(item=>item.cursos >= cantidad)
    }

    setCursosAmostrar(filtrarVectorCursosOriginal)
}



const copiarCurso = (id)=>{
    setCopiarUnCurso(true)
    setCrearCurso(false)
    setCursoAcopiar(id)
    setTimeout(() => {
        hacerScroll("nuevo-curso")
    }, 600);
}

const cambiarTipoCurso = (e)=>{
    // viene Standard, Ensamble o Instrumental
    setTipoCurso(e.target.value)
}


const cambiarCursosRecuperatorios = (e)=>{
    // viene 1 o 0 para indicar si es o no recuperatorio 
    setCursosRecuperatorios(e.target.value)
}

const iniciarNuevoCurso = ()=>{
    setCrearCurso(true);
    
    setTimeout(() => {
        hacerScroll("nuevo-curso")
    }, 600);
}

if (cargandoCursos){
return <Main center><Loading/><span className="cargando">{incluirCursadas ? 'Contando cantidad de cursadas de cada alumno...' : 'Cargando alumnos activos...'}</span></Main>
  };

  //`/curso/${curso.nro_curso}`
return(
    <Main>
        { isShowing && <Modal hide={toggle} titulo={null} isShowing={isShowing} estilo={{width:'1000px'}} estiloWrapper={{background:'tomato'}}>
            {alumnoSeleccionado && <BotonInscripcion irAinscripciones={irAinscripciones} alumno={alumnoSeleccionado}/>}
            <AbmAlumno id_alumno={alumnoSeleccionado ? alumnoSeleccionado.id_alumno : null} 
                       finalizarAltaOcopia={finalizarAltaOcopia}
                       esModal={true}
                       id_copia = {id_copia}
                       irAinscripciones={irAinscripciones}/>    
        </Modal>}

        <div className="bg-blue text-white p-4 rounded relative mt-25"> 
        {cuatrimestreActivo && 
            <Cabecera cuatrimestreActivo={cuatrimestreActivo} 
                      iniciarCrearAlumno={iniciarCrearAlumno} 
                      refrescarLista={refrescarLista}
                      incluirCursadas={incluirCursadas}
                      cargandoCursosAlumno={cargandoCursosAlumno}
                      setIncluirCursadas={setIncluirCursadas}
                      cursosAmostrar={cursosAmostrar}/>
        }

        <div className="flex f-row resultados absolute">
            <span>{cursosAmostrar.length== 1 ? `1 alumno encontrado`:`${cursosAmostrar.length} alumnos encontrados`}</span> 
            
            <Recientes recientes={recientes} setAlumnoSeleccionado={setAlumnoSeleccionado} toggle={toggle}/>
            
            <a onClick={(e)=>e.preventDefault()} className="cursor-pointer mr-2 ml-6 text-white relative" href={crearMailToListaEmails(listaEmailsSeleccion)}>
                <FontAwesomeIcon className="cursor-pointer" icon={faUserCheck}/><FontAwesomeIcon className="cursor-pointer absolute left--10" icon={faEnvelope}/> Mail a los seleccionados
            </a> 
            
            <a onClick={(e)=>e.preventDefault()} title="Enviar un mail a todos los usuarios de la lista" className="cursor-pointer mr-2 ml-2 text-white" href={crearMailToListaEmails(listaEmails)}>
                <FontAwesomeIcon className="cursor-copy" icon={faMailBulk}/> Mail grupal
            </a> 
        </div>  
        
        <table className="table mt-4">
            {/*<thead className="bg-blue-500 text-white ">
                <tr>
                    <td scope="col">#ID</td>
                   {incluirCursadas && <td title="Cantidad de cursos actuales" scope="col">
                        <div className="flex flex-wrap">
                            <span>Cursos</span>
                            { cantidad!="-1" &&  <div>    
                                <button className="botones-igual-mayor" title="Exactamente igual" onClick={()=>procesarSeleccionCantidad(true)}>
                                        <FontAwesomeIcon className={exactamenteIgual ? 'seleccionado' : 'no-seleccionado'} icon={faEquals}/>
                                </button>
                                <button className="botones-igual-mayor" title="Igual o mayor" onClick={()=>procesarSeleccionCantidad(false)} >
                                        <FontAwesomeIcon className={exactamenteIgual ? 'no-seleccionado' : 'seleccionado'} icon={faGreaterThanEqual}/>
                                </button>  
                            </div>    }  
                        </div>
                    </td> }
                    <td scope="col">Nombre</td>
                    <td scope="col">Instrumentos</td>
                    <td scope="col">Localidad</td>
                    <td scope="col">Provincia</td>
                    <td scope="col">País</td>
                    <td scope="col">Celular</td>
                    <td scope="col">Teléfono</td>

                  </tr>
            </thead>*/}
            <tbody>
            <tr>
                    <td scope="col">#ID</td>
                    {/*<td scope="col"><Seleccionador valor={tipo} onchange={handleChangeSelectTipo} vector = {tipos}/></td>*/}
                    { incluirCursadas && 
                    <td className="filas-lista mw-120" scope="col"><Seleccionador nombre='Cursos' valor={cantidad} onchange={handleChangeSelectCantidad} vector = {cantidadesCursos}/>
                        { cantidad!="-1" &&  <div className="contenedor-select-mymn">    
                                <FontAwesomeIcon title="Exactamente igual al valor seleccionado" onClick={()=>procesarSeleccionCantidad(true)} className={exactamenteIgual ? 'seleccionado cursor-pointer' : 'no-seleccionado cursor-pointer'} icon={faEquals}/>
                                <FontAwesomeIcon title="Igual o mayor al valor seleccionado" onClick={()=>procesarSeleccionCantidad(false)} className={exactamenteIgual ? 'no-seleccionado cursor-pointer' : 'seleccionado cursor-pointer'} icon={faGreaterThanEqual}/>
                            </div>    } 
                    </td>
                    
                    }
                    <td className="filas-lista mw-120x" scope="col"><Seleccionador nombre='Alumno' valor={alumno} onchange={handleChangeSelectAlumno} vector = {alumnos}/></td>
                    <td className="filas-lista mw-120x" scope="col"><Seleccionador  nombre='Instrumento' valor={instrumento} onchange={handleChangeSelectInstrumento} vector = {instrumentos}/></td>
                    <td className="filas-lista mw-120x" scope="col"><Seleccionador  nombre='Localidad' valor={localidad} onchange={handleChangeSelectLocalidad} vector = {localidades}/></td>
                    <td className="filas-lista mw-120x" scope="col"><Seleccionador  nombre='Provincia' valor={provincia} onchange={handleChangeSelectProvincia} vector = {provincias}/></td>
                    <td className="filas-lista mw-120x" scope="col"><Seleccionador nombre='Pais' valor={pais} onchange={handleChangeSelectPais} vector = {paises}/></td>
                    <td className="filas-lista mw-120x" scope="col">Celular</td>
                    <td className="filas-lista mw-120x" scope="col">Teléfono</td>
                    <td className="filas-lista mw-120x" scope="col">E-mail</td>
                    {/*<td className="filas-lista-alumnos mw-120" scope="col"><SeleccionadorEmail valor={statusMail} onchange={handleChangeSelectStatusMail}/></td>*/}
                </tr>
            {
                cursosAmostrar.map(curso => {
                return (
                    <tr key={curso.id_alumno} className="bg-blueTabla">
                        <td className="filas-lista mw-120" >{curso.id_alumno}</td>
                        {incluirCursadas && <td>{curso.cursos}</td>}
                        <td onClick={()=>{setAlumnoSeleccionado(curso);
                                     toggle()}} title={curso.alumno} className="filas-lista cursor-pointer" >
                                {curso.alumno}
                        </td>
                        <td onClick={irAinscripciones} className="filas-lista mw-120x" >{curso.instrumentos}</td>
                        <td title={curso.localidad} className="filas-lista mw-120x" >{curso.localidad}</td>
                        <td title={curso.provincia} className="filas-lista mw-120x" >{curso.provincia}</td>
                        <td title={curso.pais} className="filas-lista mw-120x" >{curso.pais}</td>
                        <td title={curso.celular} className="filas-lista mw-150x" >{curso.celular}</td>
                        <td title={curso.telefono} className="filas-lista mw-150x" >{curso.telefono}</td>
                        <td title={curso.email!=null && curso.email!='' ? curso.email : ''} className="filas-lista-alumnos cursor-copy p-iconos-listas width-35" >
                              <a onClick={(e)=>e.preventDefault()} className="text-white" href={crearMailToIndividual(curso.email)}><FontAwesomeIcon icon={curso.email != null && curso.email.trim()!='' ? faEnvelopeOpen : faCircle} /></a>  
                        </td>
                        <td title="Copiar alumno SOLO EN TESTS" onClick={()=>{setid_copia(curso.id_alumno);
                                     toggle()}} className="filas-lista cursor-copy p-iconos-listas width-35" >
                                <FontAwesomeIcon icon={faCopy}/>
                        </td>
                        <td title="Seleccionar el alumno e ir a inscripción de cursos" onClick={()=>irAinscripciones(curso)} className="filas-lista-alumnos cursor-pointer p-iconos-listas width-35" >
                                <FontAwesomeIcon icon={faUsers}/>
                        </td>
                     </tr>
                   )
                })
            }
            </tbody>
        </table>
      </div>
    </Main>
)
    }

function SeleccionadorEmail({onchange,valor}){
    return (            
        <div className="input-field col s12">
            <select value={valor} onChange = {onchange} className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                <option value="-1" key="email--1">E-mail</option>
                <option value="0" key="email-0">Sin email</option>
            </select>
        </div>
        )
}

function Seleccionador({vector,onchange,valor,sinTexto,nombre}){

    return (            
        <div className="input-field col s12">
            <select value={valor} onChange = {onchange} className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                <option value="-1" key="-1">{sinTexto ? '' : nombre}</option>
                {vector.map(item=><option value={item} key={item}>{item}</option> )}
            </select>
        </div>
        )
        
}    

function Cabecera({cuatrimestreActivo,
                   refrescarLista,
                   iniciarCrearAlumno,
                   incluirCursadas,
                   cargandoCursosAlumno,
                   setIncluirCursadas}){
    return <div className="absolute cableft flex f-row">

            <span className="cabecera mr-4">{`Listado de alumnos activos`}</span>   

            <span title="Refrescar la lista" onClick={()=>refrescarLista()} 
                        className="cursor-pointer acciones-lista-cabecera mr-4" >
                            <FontAwesomeIcon className="cursor-copy" icon={faSync}/> Refrescar
            </span>
            <span title="Crear un nuevo alumno" onClick={()=>iniciarCrearAlumno()} 
                        className="cursor-pointer acciones-lista-cabecera mr-4" >
                            <FontAwesomeIcon className="cursor-copy" icon={faPlusSquare}/> Crear un nuevo alumno
            </span>
            {incluirCursadas==0 && !cargandoCursosAlumno && 
            <span title="Volver a generar la lista incluyendo la cantidad de cursadas actuales de cada alumno" 
                className="cursor-pointer acciones-lista-cabecera mr-4" onClick={()=>setIncluirCursadas(true)} >
                <FontAwesomeIcon className="cursor-copy" icon={faHashtag}/> Incluir la cantidad de cursadas de cada alumno
            </span>}
            {cargandoCursosAlumno && <div className="flex f-row acciones-lista-cabecera"><Loading/><span className="text-black">Contando cantidad de cursadas de cada alumno...</span></div>}

    </div>
}

function TipoCursos({cambiarTipoCurso,cambiarCursosRecuperatorios, tipoCurso, cursoRecuperatorio}){
    return (
        <div className="flex f-row selecTipoCurso absolute">

            <span title="Curso Regular o Mesa de examen" className="tipo-curso mr-4 ml-4">R/ME</span>

            <select title="Curso Regular o Mesa de examen" value={cursoRecuperatorio} 
                className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline" name="" id="" 
                onChange={cambiarCursosRecuperatorios}>
                <option value="-1">Seleccionar</option>
                <option value="false">Regulares</option>
                <option value="true">Recuperatorios</option>
            </select>

            <span title="Curso Standard, Instrumental o Ensamble" className="tipo-curso mr-4 ml-4">Tipo</span>

            <select title="Curso Standard, Instrumental o Ensamble" value={tipoCurso} 
                className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline" name="" id="" 
                onChange={cambiarTipoCurso}>
                <option value="-1">Seleccionar</option>
                <option value="Standard">Standard</option>
                <option value="Instrumental">Instrumental</option>
                <option value="Ensamble">Ensamble</option>
            </select>

        </div>

    )
    
}

